export const ListItemCharacter = ({ dataEl, filterItems, sortItem }) => {

  return (
    <Grid container sx={{ fontSize: '14px' }}>
      <Grid item sx={{ width: width.status }}>
        <div style={{ display: 'flex', height: 60, }}>
          <div style={{ width: 60 }}>
            <Img name={dataEl.name} square={60} borderRadius={30} />
          </div>
          <Stack >
            <div style={{ display: 'flex' }}>
              <div style={{ height: 40, display: 'flex', alignItems: 'center', marginLeft: '4px' }}> {/* marginLeftを追加して左に隙間を設ける */}
                <Img name={dataEl.role} square={30} borderRadius={5} />
              </div>
              <div style={{ paddingLeft: 4, lineHeight: '1' }} >
                <div>
                  <Img name={dataEl.strongAttribute} square={20} rhombus={true} />
                  {[...Array(dataEl.star)].map((_, index) => (
                    <StarIcon key={index} style={{ color: 'yellow', fontSize: '16px', marginRight: -3 }} />
                  ))}
                  <span style={{ fontSize: '12px' }}>
                    {(dataEl.name === 'レスナ【未来に夢を求めし者】' || dataEl.name === 'ヴァレリア【過去に夢を求めし者】') && '　※数値は暫定'}
                  </span>
                </div>
                {dataEl.name}

              </div>
            </div>
            <div style={{ display: 'flex' }}>
              {Object.entries(dataEl.status).map(([key, value]) => {
                const isSortItem = sortItem === key
                return (
                  <div key={key} style={statusStyle}>
                    <div style={statusChartStyle(value, 'character', key, isSortItem)}></div>
                    <div style={statusValueStyle}>{value.toLocaleString()}</div>
                  </div>
                )
              })}
            </div>
          </Stack>
        </div>
      </Grid>
      <Grid item sx={{ width: width.skill }}>
        <div style={{ alignItems: 'center', width: 280 }} >
          {dataEl.skill.map((skillName, skillIndex) => {
            const isFilterItemAttribute = filterItems.attribute.length > 0 && filterItems.attribute.includes(skill[skillName].attribute)
            const isFilterItemArea = filterItems.area.length > 0 && filterItems.area.includes(skill[skillName].area)
            return (
              <div key={skillName} style={{ width: 280, display: 'flex' }}>
                <div style={skillAreaStyle(isFilterItemAttribute)}>
                  <Img name={skill[skillName].attribute} square={20} rhombus={true} />
                </div>
                <div style={skillAreaStyle(isFilterItemArea)}>
                  {skill[skillName].area}
                </div>
                <SkillStatusView
                  skillName={skillName}
                  skillStatus={skillStatus}
                  sortItem={sortItem}
                  skillIndex={skillIndex}
                />
                {skill[skillName].effects && skill[skillName].effects.map((effectEl) => {
                  return (
                    <Chip
                      key={effectEl.text}
                      label={effectEl.text}
                      size="small"
                      sx={chipStyle}
                    />
                  )
                })}
              </div>
            )
          })}
        </div>
      </Grid>

      {/* アビリティ */}
      <Grid item sx={{ width: width.charAbility }}>
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: 240, alignItems: 'flex-start' }}>
          {dataEl.ability.map(abilityName => {

            const isFilterItem = filterItems?.charAbility.length > 0 && filterItems.charAbility.includes(abilityName)

            return (
              <Tooltip key={abilityName}
                placement="right"
                arrow
                title={<TextBr text={charAbility[abilityName].text} name={abilityName} />}
              >
                <Chip
                  label={abilityName}
                  size="small"
                  sx={{
                    ...chipStyle,
                    padding: '1px',
                    backgroundColor: isFilterItem ? darken(checkColor, 0.2) : undefined,
                    color: mainColor,
                  }}
                />
              </Tooltip>
            )

          })}
        </div>
      </Grid>

      {/* ギフト */}
      <Grid item sx={{ width: width.gift, display: 'flex' }}>
        {/* <div style={{ width: 30, height: 60 }}>
        <GiftColor color={dataEl.giftColor} />
      </div> */}
        <Gift gifts={dataEl.gift} filterItems={filterItems} />

      </Grid>
    </Grid>

  )



}

export const DetailCharacter = ({ dataEl, filterItems }) => {

  return (
    <Grid container sx={{ fontSize: '14px' }}>
      <Grid item>
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', height: '100%' }} >
          {dataEl.skill.map((skillName, skillIndex) => {
            const isFilterItemSkill = filterItems.skill.length > 0 && filterItems.skill.includes(skillName)
            const isFilterItemAttribute = filterItems.attribute.length > 0 && filterItems.attribute.includes(skill[skillName].attribute)
            const isFilterItemArea = filterItems.area.length > 0 && filterItems.area.includes(skill[skillName].area)

            return (
              <div key={skillName} style={{ display: 'flex' }}>
                <div style={skillAreaStyle(isFilterItemAttribute)}>
                  <Img name={skill[skillName].attribute} square={20} rhombus={true} />
                </div>
                <div style={skillAreaStyle(isFilterItemArea)}>
                  {skill[skillName].area}
                </div>
                <Chip
                  key={skillName}
                  label={skillName}
                  size="small"
                  sx={{
                    height: 18, fontSize: '12px', padding: '1px', margin: '1px',
                    backgroundColor: isFilterItemSkill ? darken(checkColor, 0.2) : undefined,
                    color: isFilterItemSkill ? mainColor : undefined,
                  }}

                />
                <div style={{ fontSize: '12px', paddingTop: '1px' }}>
                  <TextBr text={skill[skillName].text} />
                </div>
              </div>
            )
          })}
        </div>
      </Grid>
    </Grid>
  )
}

export const ListItemSkill = ({ dataEl, filterItems, sortItem }) => {
  const isFilterItemAttribute = filterItems.attribute.length > 0 && filterItems.attribute.includes(dataEl.attribute)
  const isFilterItemArea = filterItems.area.length > 0 && filterItems.area.includes(dataEl.area)

  let checkedTexts = []
  if (filterItems.skillEffectItem.length > 0) {
    if (dataEl.effects) {
      for (const effectEl of dataEl.effects) {
        if (isAnyMatch(filterItems.skillEffectItem, Object.keys(effectEl.effectItems))) {
          checkedTexts.push(effectEl.text)
        }
      }
    }
  }

  return (
    <Grid container sx={{ fontSize: '14px', alignItems: 'center' }}>
      <Grid item sx={{ width: 60, height: 20, fontSize: '12px' }}>
        {dataEl.number}
      </Grid>
      <Grid item sx={{ width: 160, height: 20 }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 280 }}>
        <div style={{ alignItems: 'center' }} >
          <div style={{ display: 'flex' }}>
            <div style={skillAreaStyle(isFilterItemAttribute)}>
              <Img name={dataEl.attribute} square={20} rhombus={true} />
            </div>
            <div style={skillAreaStyle(isFilterItemArea)}>
              {dataEl.area}
            </div>
            <SkillStatusView
              skillName={dataEl.name}
              skillStatus={currentSkillStatusKeys}
              sortItem={sortItem}
            />
          </div>
        </div>
      </Grid>
      <Grid item sx={{ fontSize: '12px', pl: '4px' }}>
        <TextBr text={dataEl.text} checkedTexts={checkedTexts} />
      </Grid>
    </Grid>
  )
}



