const equipmentStatusMap = {
  '武器': ['物攻', '魔攻'],
  '防具': ['物防', '魔防'],
  '装飾品': ['HP', '素早さ'],
}

const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    fontSize: '10px', // これは既に設定されていますが、必要に応じてさらに小さく調整できます。
    color: mainColor,
    backgroundColor: darken(mainBackgroundColor, 0.3),
    right: -4,
    top: 12,
    padding: '0 4px', // このパディングは、バッジのサイズを制御します。調整して理想的なサイズを見つけてください。
    minWidth: '16px', // 最小幅を設定することで、バッジのサイズが小さくなりすぎるのを防ぎます。
    height: '16px', // バッジの高さ。これを調整して、希望するバッジのサイズに近づけます。
    borderRadius: '8px', // 必要に応じて調整してください。これはバッジの角の丸みを制御します。
  },
}));



export const ListItemItem = ({ dataEl, filterItems, sortItem, onChangeTab }) => {

  let checkedTexts = []
  if (filterItems.effectItemOfItem.length > 0) {
    if (dataEl.effects) {
      for (const effectEl of dataEl.effects) {
        if (isAnyMatch(filterItems.effectItemOfItem, Object.keys(effectEl.effectItems))) {
          checkedTexts.push(effectEl.text)
        }
      }
    }
  }



  return (
    <Grid container sx={{ fontSize: '14px', alignItems: 'center' }}>
      <Grid item sx={{ width: 80, height: 20, display: 'flex', alignItems: 'center' }}>
        <ButtonToSwitchAlchemyTab onChangeTab={onChangeTab} dataEl={dataEl} />
      </Grid>
      <Grid item sx={{ width: 20, height: 20 }}>
        <Img name={dataEl.kind2} square={20} borderRadius={10} rhombus={!dataEl.kind2.includes('アイテム')} />
      </Grid>
      <Grid item sx={{ width: 180, height: 20, paddingLeft: '4px' }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 200, height: 20 }}>
        <div style={{ display: 'flex', width: 360 }}>
          {dataEl.kind1 === '装備アイテム'
            ?
            equipmentStatusMap[dataEl.kind2].map((key) => {
              if (dataEl.status[key]) {
                const isSortItem = sortItem === key
                return (
                  <div key={key} style={statusStyle}>
                    <div style={statusChartStyle(dataEl.status[key], 'item', key, isSortItem)}></div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%', zIndex: 1 }}>
                      <span style={{ fontSize: '10px', textAlign: 'left', paddingLeft: '2px' }}>{key}</span>
                      <span style={{ fontSize: '14px', textAlign: 'right', paddingRight: '4px' }}>{dataEl.status[key]}</span>
                    </div>
                  </div>
                )
              } else {
                return (
                  <div key={key} style={{ width: 60 }}>
                  </div>
                )

              }
            })
            : <div style={{ display: 'flex' }}>
              <div style={{ width: 20, textAlign: 'center' }}>
                {dataEl.attribute &&
                  <Img name={dataEl.attribute} square={20} rhombus={true} />
                }
              </div>
              <div style={{ width: 20, textAlign: 'center' }}>{dataEl.area}</div>
              <div style={{ width: 80, textAlign: 'center' }}>
                <span style={{ fontSize: '12px' }}>使用回数 </span>
                <span style={{ fontSize: '14px' }}>{dataEl.usageCount}</span>
              </div>
              {Object.keys(skillStatusMap).map((key) =>
                dataEl[key] && (
                  <div key={key} style={{ display: 'flex' }}>
                    <Img name={key} square={20} borderRadius={5} />
                    <span>{dataEl[key].toLocaleString() + (dataEl.fixed ? '' : '%')}</span>
                  </div>
                )
              )}
            </div>
          }
        </div>
      </Grid>
      <Grid item sx={{ width: 540, fontSize: '12px' }} >
        <TextBr text={dataEl.text} checkedTexts={checkedTexts} name={dataEl.name} />
      </Grid>
      <Grid item
        sx={{
          // width: 300,
          height: 'auto',
          fontSize: '12px',
          paddingLeft: '4px',
          display: 'flex',
          alignItems: 'center',
          gap: '10px', // ボタンとバッジ間のスペース
        }}
      >
        {dataEl.materials && Object.entries(dataEl.materials).map(([materialName, pieces], index) => {
          return (
            <div key={index} style={{ display: 'flex', alignItems: 'center' }}>
              <StyledBadge badgeContent={pieces} sx={{  marginRight: '4px' ,zIndex:2}}>
                <ButtonToSwitchTabs
                  onChangeTab={onChangeTab}
                  name={materialName}
                  category={'material'}
                  filterItemKey={'material'}
                />
              </StyledBadge>
            </div>
          );
        })}
      </Grid>

    </Grid>
  )
}

export const ListItemMaterial = ({ dataEl, filterItems, sortItem, onChangeTab }) => {
  return (
    <Grid container sx={{ height: '100%', display: 'flex', alignItems: 'stretch' }}>
      <Grid item sx={{ width: 40, display: 'flex', alignItems: 'center' }}>
        <Img name={dataEl.target} square={30} borderRadius={15} />
      </Grid>
      <Grid item sx={{ width: 40, display: 'flex', alignItems: 'center' }}>
        {dataEl.rarity}
      </Grid>
      <Grid item sx={{ width: 40, display: 'flex', alignItems: 'center' }}>
        <GiftColor1 color={dataEl.giftColor} size={15} />
      </Grid>
      <Grid item sx={{ width: 120, fontSize: '14px', display: 'flex', alignItems: 'center' }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 200, display: 'flex', alignItems: 'center' }}>
        <Gift gifts={dataEl.gift} filterItems={filterItems} />
      </Grid>
      <Grid item sx={{ width: 330, fontSize: '12px' }}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          {Object.entries(dataEl.quest).map(([questName,pieces]) => (
            <div key={questName} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <div style={{ width: 280, textAlign: 'right' }}>
                <StyledBadge badgeContent={quest[questName].materials[dataEl.name]} sx={{ width:'100%', marginRight: '4px',zIndex:9 }}>
                  <ButtonToSwitchTabs
                    key={questName}
                    onChangeTab={onChangeTab}
                    name={questName}
                    category={'quest'}
                    filterItemKey={'quest'}
                    fullWidth={true}
                  />
                </StyledBadge>

              </div>
              <div style={{ width: 50, textAlign: 'right' }}>{quest[questName].recommendedPower && quest[questName].recommendedPower.toLocaleString()}</div>
            </div>
          ))}
        </div>
      </Grid>
      <Grid item sx={{ width: 180, marginLeft: '10px' }}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          {Object.entries(dataEl.items).map(([itemName, pieces]) => {
            return (
              // <div key={itemName} style={{ width: '100%' }}>
              <StyledBadge
                key={itemName}
                badgeContent={pieces}
                right={-10}
              >
                <ButtonToSwitchTabs
                  key={itemName}
                  onChangeTab={onChangeTab}
                  name={itemName}
                  category={'item'}
                  filterItemKey={'item'}
                  fullWidth={true}
                />
              </StyledBadge>
              // </div>
            )
          })}
        </div>
      </Grid>
    </Grid>
  )
}

export const ListItemGift = ({ dataEl, filterItems, sortItem, onChangeTab }) => {
  return (
    <Grid container>
      <Grid item sx={{ width: 30, display: 'flex', alignItems: 'center' }}>
        <Img name={dataEl.target} square={30} borderRadius={15} />
      </Grid>
      <Grid item sx={{ width: 30, display: 'flex', alignItems: 'center' }}>
        <Img name={dataEl.giftType} square={30} borderRadius={15} />
      </Grid>
      <Grid item sx={{ width: 200, fontSize: '14px', display: 'flex', alignItems: 'center', pl: '4px' }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 400, fontSize: '12px', display: 'flex', alignItems: 'center' }}>
        <TextBr text={dataEl.text} />
      </Grid>
      <Grid item sx={{ fontSize: '12px', display: 'flex', alignItems: 'center' }}>
        {dataEl.owner.character.map((charName) => {
          return (
            <ButtonToSwitchTabs
              key={charName}
              onChangeTab={onChangeTab}
              name={charName}
              category={'character'}
              filterItemKey={'character'}
            />
          )
        })}
        {dataEl.owner.material.map((materialName) => {
          return (
            <ButtonToSwitchTabs
              key={materialName}
              onChangeTab={onChangeTab}
              name={materialName}
              category={'material'}
              filterItemKey={'material'}
            />
          )
        })}
      </Grid>
    </Grid>
  )
}




export const ListItemAlchemy = ({ dataEl, filterItems, sortItem, onChangeTab }) => {

  return (
    <Grid container>
      <Grid item sx={{ width: 1000, height: 40, display: 'flex', alignItems: 'center' }}>
        <div style={{ display: 'flex', alignItems: 'center' }}>

          <ButtonToSwitchTabs
            onChangeTab={onChangeTab}
            name={dataEl.names[0]}
            category={'character'}
            filterItemKey={'character'}
          />
          <div style={{ width: width.gift }}>
            <Gift
              gifts={dataEl.target === '装備アイテム' ? [dataEl.gift[0]] : [dataEl.gift[0], dataEl.gift[1]]}
              filterItems={filterItems}
            />
          </div>

          <ButtonToSwitchTabs
            onChangeTab={onChangeTab}
            name={dataEl.names[1]}
            category={'character'}
            filterItemKey={'character'}
          />
          <div style={{ width: width.gift }}>
            <Gift
              gifts={dataEl.target === '装備アイテム' ? [dataEl.gift[1]] : [dataEl.gift[2], dataEl.gift[3]]}
              filterItems={filterItems}
            />
          </div>

          <div style={{ width: 130, fontSize: '14px' }}>
            <ButtonToSwitchTabs
              onChangeTab={onChangeTab}
              name={dataEl.names[2]}
              category={'material'}
              filterItemKey={'material'}
              fullWidth={true}
            />
          </div>
          <div style={{ width: width.gift - 30 }}>
            <Gift
              gifts={dataEl.target === '装備アイテム' ? [dataEl.gift[2], dataEl.gift[3]] : [dataEl.gift[4], dataEl.gift[5]]}
              filterItems={filterItems}
            />
          </div>
        </div>
      </Grid>
    </Grid>
  )
}

export const ListItemQuest = ({ dataEl, filterItems, sortItem, onChangeTab }) => {
  return (
    <Grid container>
      <Grid item sx={{
        width: 300, display: 'flex', alignItems: 'center', flexDirection: 'column',
        alignItems: 'flex-end',
      }}>
        <AlignedText text={dataEl.name} />
        
      </Grid>
      <Grid item sx={{
        width: 60, display: 'flex', alignItems: 'center', flexDirection: 'column',
        alignItems: 'flex-end',
      }}>
        {dataEl.recommendedPower && dataEl.recommendedPower.toLocaleString()}
      </Grid>
      <Grid item
        sx={{
          width: 720,
          height: 'auto',
          fontSize: '12px',
          paddingLeft: '10px',
          display: 'flex',
          alignItems: 'center',
          gap: '10px', // ボタンとバッジ間のスペース
        }}
      >
        {dataEl.materials && Object.entries(dataEl.materials).map(([materialName, pieces], index) => {
          return (
            <div key={index} style={{ display: 'flex', alignItems: 'center' }}>
              <StyledBadge badgeContent={pieces} sx={{ marginRight: '4px' }}>
                <ButtonToSwitchTabs
                  onChangeTab={onChangeTab}
                  name={materialName}
                  category={'material'}
                  filterItemKey={'material'}
                />
              </StyledBadge>
            </div>
          );
        })}
      </Grid>


    </Grid>
  )
}




