


export const Img = ({ name, square, borderRadius = 0, rhombus }) => {
  return (
    <img
      src={`${process.env.PUBLIC_URL}/${name}.png`}
      style={{
        width: square,
        height: square,
        borderRadius,
        clipPath: rhombus ? 'polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%)' : '',
      }}
      alt={name}
    />
  );
};