import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'

import { mainBackgroundColor , checkColor,mainColor } from '../utils/commonStyle';
import  gift  from '../constants/gift';
import { Img } from './Img'
import { TextBr } from './Text'
import { GiftColor, GiftColor1 } from './GiftColor'




export const Gift = ({ gifts, filterItems }) => {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: 240, alignItems: 'flex-start' }}>
      {gifts.map(giftName => {
        if (!gift[giftName]) console.log(giftName)
        const isFilterItem = filterItems?.gift.length > 0 && filterItems.gift.includes(giftName)
        const isFilterItem2 = filterItems?.gift2 && filterItems?.gift2.length > 0 && filterItems.gift2.includes(giftName)
        return (
          <Tooltip
            key={giftName}
            placement="right"
            arrow
            title={<TextBr text={gift[giftName].text} name={giftName} />}
          >
            <Chip
              icon={<Img name={gift[giftName].giftType} square={16} borderRadius={8} />}
              label={giftName}
              size="small"
              sx={{
                height: 18, fontSize: '12px', padding: '1px', margin: '1px',
                backgroundColor: isFilterItem ? darken(checkColor, 0.2) :isFilterItem2 ? darken(checkColor, 0.4): undefined,
                color: mainColor,
              }}
            />
          </Tooltip>
        )
      })}
    </div>
  )
}